import React from "react";
import "./index.css";

const Cookie = ({
  handleCookie,
  onHide
}) => {
  return (
    <>
      <div
        className="cookie-container"
      >
        <div className="cookie-text">
          <p>
            Utilizamos cookies e identificadores propios y de terceros con tu
            consentimiento y/o nuestro inters legtimo, con el propsito de
            almacenar o acceder a informacin en tu dispositivo, recabar datos
            personales sobre la audiencia para desarrollar y mejorar productos
            as como mostrar y medir anuncios propios y/o de terceros y/o
            contenido personalizados basndonos en tu navegacin (por ejemplo
            pginas visitadas).
          </p>
        </div>
        <div className="cookie-buttons">
          <button onClick={handleCookie}>Aceptar</button>
          <button onClick={onHide}>Cancelar</button>
        </div>
      </div>
      <div className="cookie-background"/>
    </>
  );
};

export default Cookie;
